.emptyCart {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 100px 0;

  display: none;
}
.emptyCart img {
  max-width: 100%;
}
.emptyCart p {
  font-size: 3.2rem;
  font-family: Barlow, sans-serif;
  line-height: 1.5;
  font-weight: 500;
  text-align: center;

  margin: 0 10px;
  margin-bottom: 20px;
}
.btnEmpty {
  display: flex;

  justify-content: center;
}
.btnEmpty a {
  height: 100%;
  width: 100%;
  text-decoration: none;
  text-align: center;
  font-size: 1.8rem;

  background: #222;
  color: #fff;

  padding: 15px 50px;
  margin-top: 1rem;
  border-radius: 5px;

  position: relative;
  transition: 0.3s;
}
.btnEmpty a:hover {
  background: #d1301b;
  color: white;
}

.cartSection {
  display: flex;

  justify-content: center;

  margin: 40px 5px;
}

.cartTable {
  margin-right: 35px;
}

.cartCalculate {
  display: flex;
  flex-direction: column;
}

@media (max-width: 1380px) {
  .cartTable {
    margin-right: 0px;
  }

  .cartSection {
    flex-direction: column;
    align-items: center;
  }

  .cartCalculate {
    width: 55%;
  }
}

@media (max-width: 768px) {
  .cartCalculate {
    width: 70%;
  }
}

@media (max-width: 600px) {
  .emptyCart p {
    font-size: 2rem;
  }

  .cartTable {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .cartSection {
    margin-left: 0px;
    margin-right: 0px;
  }
  .cartCalculate {
    width: 95%;
  }
}
