.PalletteContainer {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0 0 20px;
}
.singleColor {
  cursor: pointer;
  width: 30px;
  height: 30px;
  border: 1px solid #ccc;
  border-radius: 50%;
  margin-right: 20px;
}

.activeColor,
.singleColor:hover {
  border: 2px solid #000;
}
